let hr = document.getElementById("hr");
let min = document.getElementById("min");
let sec = document.getElementById("sec");
let mSec = document.getElementById("m-sec");

let hr1 = 0;
let min1 = 0;
let sec1 = 0;
let mSec1 = 0;
let hr2 = hr1;
let min2 = min1;
let sec2 = sec1;
let mSec2 = mSec1;

let timer = false;

function stopwatch() {
  if (timer === true) {
    mSec1++;

    if (mSec1 === 100) {
      sec1++;
      mSec1 = 0;
    }

    if (sec1 === 60) {
      min1++;
      sec1 = 0;
      mSec1 = 0;
    }

    if (min1 === 60) {
      hr1++;
      min1 = 0;
      sec1 = 0;
      mSec1 = 0;
    }

    hr2 = hr1;
    min2 = min1;
    sec2 = sec1;
    mSec2 = mSec1;

    if (hr1 < 10) {
      hr2 = "0" + hr2;
    }
    if (min2 < 10) {
      min2 = "0" + min2;
    }
    if (sec2 < 10) {
      sec2 = "0" + sec2;
    }
    if (mSec2 < 10) {
      mSec2 = "0" + mSec2;
    }

    hr.innerHTML = hr2;
    min.innerHTML = min2;
    sec.innerHTML = sec2;
    mSec.innerHTML = mSec2;

    setTimeout(stopwatch, 9);
  }
}

let start = document.getElementById("start");
let pause = document.getElementById("pause");
let bookmark = document.getElementById("bookmark");
let reset = document.getElementById("reset");

let logCont = document.getElementsByClassName("log-container");
let count = 0;

start.addEventListener("click", function () {
  start.style.display = "none";
  pause.style.display = "inline";
  timer = true;
  stopwatch();
});

pause.addEventListener("click", function () {
  start.style.display = "inline";
  pause.style.display = "none";
  timer = false;
});

reset.addEventListener("click", function () {
  timer = false;
  hr.innerHTML = "00";
  min.innerHTML = "00";
  sec.innerHTML = "00";
  mSec.innerHTML = "00";
  hr1 = 0;
  min1 = 0;
  sec1 = 0;
  mSec1 = 0;
  start.style.display = "inline";
  pause.style.display = "none";

  let table = document.getElementById("log");
  for (let i = 0; i < count; i++) {
    table.deleteRow(0);
  }
  logCont[0].style.visibility = "hidden";
  count = 0;
});

bookmark.addEventListener("click", function () {
  if (timer === true) {
    logCont[0].style.visibility = "visible";
    count++;
    let timeStr = hr2 + ":" + min2 + ":" + sec2 + "." + mSec2;

    let table = document.getElementById("log");
    let row = table.insertRow(0);
    let cell1 = row.insertCell(0);
    let cell2 = row.insertCell(1);
    cell1.innerHTML = count + ".";
    cell2.innerHTML = timeStr;
  }
});
